@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.SkillCard-dropdown {
  position: relative;
}

.SkillCard-dropdown-content {
  display: none;
  position: absolute;
  background-color: #282c34;
  min-width: 200px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.SkillCard-dropdown-content.show {
  display: block;
}

.SkillCard-dropdown-content ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.SkillCard-dropdown-content li {
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.SkillCard-dropdown-content li:hover {
  background-color: #ddd;
}